                class="btn btn-danger"
              >Remove Piece</button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="card" v-if="selection ==='customStrategies'">
      <div class="card-header">Add/Edit Custom Strategies</div>
      <div class="card-body">
        <div class="row">
          <div class="col">
            <select v-model="strategy" class="form-control" @change="strategySelect">
              <option value>Add New Custom Strategy</option>
              <option
                :value="strategy.label"
