                v-for="(strategy,index) in customStrategies"
                :key="index"
              >{{strategy.label}}</option>
            </select>
          </div>
        </div>
        <br />
        <div>
          <div class="row">
            <div class="col">
              <div class="input-group mb-3">
                <div class="input-group-prepend">
                  <span class="input-group-text">Name</span>
                </div>
                <input
                  id="stratLabel"
                  type="text"
                  :value="customStrategies[strategyIndex] ? customStrategies[strategyIndex].label : ''"
                  class="form-control"
                />
              </div>
            </div>
          </div>
          <div class="row">
            <div class="col">
              <div class="input-group mb-3">
                <div class="input-group-prepend">
                  <span class="input-group-text">Description</span>
                </div>
                <textarea
                  id="stratDescription"
                  :value="customStrategies[strategyIndex] ? customStrategies[strategyIndex].description : ''"
                  class="form-control"
                ></textarea>
              </div>
            </div>
          </div>
          <div class="row">
            <div class="col">
              <div class="form-check form-check-inline">
                <input
                  id="stratMaster"
                  class="form-check-input"
                  type="checkbox"
                  :checked="customStrategies[strategyIndex] ? customStrategies[strategyIndex].master : false"
                  :value="customStrategies[strategyIndex] ? customStrategies[strategyIndex].master : false"
                />
                <label for="stratMaster" class="form-check-label">Master List Submission</label>
              </div>
            </div>
            <transition name="fade">
              <div
                v-if="updated"
                class="alert alert-success"
                role="alert"
              >{{(strategy !== '') ? 'Updated!' : 'Added!'}}</div>
            </transition>
            <div class="col">
              <button
                @click="updateStrategy"
                class="btn btn-success"
              >{{(strategy !== '') ? 'Update ' : 'Add '}} Strategy</button>
            </div>
            <div v-if="strategy" class="col">
              <button
                @click="removeCustomEntry('customStrategies',customStrategies[strategyIndex].id)"
                class="btn btn-danger"
              >Remove Strategy</button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-if="selection ==='sheets'">
      <div class="row">
        <ul class="nav nav-pills">
          <li class="nav-item" @click="sheetsActive = 'sheets'">
            <a class="nav-link" v-bind:class="{ active: sheetsActive === 'sheets' }" href="#">Sheets</a>
          </li>
          <li class="nav-item" @click="sheetsActive = 'templates';setSelection(null);">
            <a
              class="nav-link"
              v-bind:class="{ active: sheetsActive === 'templates' }"
              href="#"
            >Templates</a>
          </li>
        </ul>
      </div>
      <br />
      <br />

      <div v-if="sheetsActive === 'sheets' && sheets">
        <div class="row">
          <div class="col-md-8 wp-bp-content-width">
            <div id="wp-bp-posts-slider" class="carousel slide mt-3r rounded" data-ride="carousel">
              <ol class="carousel-indicators">
                <li data-target="#wp-bp-posts-slider" data-slide-to="0" class="active"></li>
                <li data-target="#wp-bp-posts-slider" data-slide-to="1"></li>
                <li data-target="#wp-bp-posts-slider" data-slide-to="2"></li>
              </ol>
              <div class="carousel-inner rounded">
                <div class="carousel-item rounded" style="height:300px;background-color:#343a40;" v-bind:class="{ active: ind === 0 }" v-for="(sheet,i,ind) in filterSheets(sheets,true)" :key="i">
                  <div class="carousel-caption d-none d-md-flex align-items-end">
                    <div class="w-100 text-center mb-4">
                      <h5>{{sheet.entryName}}</h5>
                      <p>{{formatWeek(sheet.week)}}</p>
                      <button
                        class="btn btn-primary btn-sm"
                        v-on:click="goToSheet(i,$event)"
                      >
                        View Sheet
                      </button>
                    </div>
                  </div>
                </div>
              </div>
              <a
                class="carousel-control-prev"
                href="#wp-bp-posts-slider"
                role="button"
                data-slide="prev"
              >
                <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                <span class="sr-only">Previous</span>
              </a>
              <a
                class="carousel-control-next"
                href="#wp-bp-posts-slider"
                role="button"
                data-slide="next"
              >
                <span class="carousel-control-next-icon" aria-hidden="true"></span>
                <span class="sr-only">Next</span>
              </a>
            </div>
          </div>

          <div class="col-md-4 wp-bp-sidebar-width">
            <aside id="secondary" class="widget-area sidebar-1-area mt-3r card">
              <div class="list-group">
                <a
                  href="#"
                  class="list-group-item list-group-item-action flex-column align-items-start"
                >
                  <div class="d-flex w-100 justify-content-between">
                    <h5 class="mb-1">Practice Sheets</h5>
                    <br />
                    <br />
                    <!--       <small class="text-muted">3 days ago</small> -->
                  </div>
                  <p class="mb-1">Select Folder to view sheets</p>
                  <br />
                  <!--     <small class="text-muted">Donec id elit non mi porta.</small> -->
                  <div class="dropdown">
                    <button
                      class="btn dropdown-toggle border"
                      type="button"
                      id="dropdownMenuButton"
                      data-toggle="dropdown"
                      aria-haspopup="true"
                      aria-expanded="false"
                    >
                    {{selectedFolder ? selectedFolder : 'Select Folder'}}
                    </button>
                    <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                      <a
                        @click="setSelection(folder)"
                        class="dropdown-item"
                        v-for="(count,folder) in folderCountObj"
                        :key="folder"
                      >{{folder}}</a>
                      <a v-if="!folderCountObj['Unassigned']"
                        @click="setSelection('Unassigned')"
                      >
                          Unassigned
                      </a>
                    </div>
                  </div>
                  <br />
                </a>
              </div>
            </aside>
            <!-- #secondary -->
          </div>
        </div>

        <br />
        <br />

        <div class="row">
          <div class="col-lg-6 col-sm-12 selection-item">
            <div class="card h-100">
              <div class="card-body">
                <h4 class="card-title">
                  <span>Practice Sheets</span>
                </h4>
                <div v-for="(sheet,i) in filterSheets(sheets)" :key="i">
                  <a
                    v-on:click="goToSheet(i,$event)"
                    href
                  >{{sheet.entryName + ' ' + formatWeek(sheet.week)}}</a>
                  <Remove @removeEntry="removeSheet(i)" />
                </div>
              </div>
            </div>
          </div>
          <div class="col-lg-6 col-sm-12 selection-item">
            <div class="card" v-for="(entry,i) in selectedFolderEntries" :key="i">
                <div class="card-body">
                    <h5>{{entry.entryName}}</h5>
                </div>
            </div>
          </div>
        </div>
      </div>

      <div v-if="sheetsActive === 'templates'" class="row">
        <div class="col-lg-6 col-sm-12 selection-item">
          <div class="card h-100">
            <div class="card-body">
              <h4 class="card-title">
                <span>Practice Sheet Templates</span>
              </h4>
              <br />
              <button @click="goToTemplate" class="btn btn-success">Create New Template</button>
              <br />
              <br />
              <br />
              <ul v-if="!selectedFolder" class="list-group">
                <li
                  v-on:click="setSelection(folder)"
                  class="list-group-item d-flex justify-content-between align-items-center click"
                  v-for="(count,folder) in folderCountObj"
                  :key="folder"
                  :class="{active:selectedFolder===folder}"
                >
                  {{folder}}
                  <span class="badge badge-primary badge-pill">{{count}}</span>
                </li>
              </ul>
              <div class="card" v-if="selectedFolder">
                <div class="card-body">
                  <h4>{{selectedFolder}} Sheet Templates</h4>
                  <button
                    @click="$store.commit('user/setSelection',null);"
                    class="btn btn-info"
                  >Go Back</button>
                </div>
              </div>
              <br />
              <div v-if="selectedFolder">
                <div class="card" v-for="(entry,i) in selectedFolderEntries" :key="i">
                  <div class="card-body">
                    <h5>{{entry.entryName}}</h5>
                    <Edit @editEntry="goToEntry(i)" />
                    <EntryAdd class="margin-left" @addEntry="addEntry(entry)" />
                    <Assign
                      class="margin-left"
                      v-if="isInstructor"
                      @assignEntry="assignEntry(entry)"
                    />
                    <Remove class="margin-left" @removeEntry="removeEntry(i)" />
                    <br />
                    <label>Folder: {{selectedFolder}}</label>
                    <br />
                    <button
                      class="btn btn-secondary"
                      @click="showUpdateFolderModal(entry,i)"
                    >Update Folder</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <br />
    <Modal v-if="showModal" @close="doAssignEntry">
      <h3 slot="header">Share Sheet With Student</h3>
      <div slot="body">
        <select v-model="studentSelect" class="form-control">
          <option value>Select Student</option>
          <option v-for="(student,key) in myStudents" :key="key" :value="key">{{student.name}}</option>
        </select>
      </div>
    </Modal>
    <Modal
      v-if="showFolderUpdateModal"
      @close="folderUpdateModalClosed"
      btnText="Update"
      wide="true"
    >
      <h3 slot="header">
        {{modalEntry.name}}
        <br />Update Folder
      </h3>
      <div slot="body">
        <multiselect
          :allow-empty="false"
          v-model="folderSelect"
          :taggable="true"
          @tag="addTagPiece"
          :options="myFolders.concat(myTags)"
        ></multiselect>
      </div>
    </Modal>
  </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";

import Edit from "./../icons/Edit.vue";
import EntryAdd from "./../icons/EntryAdd.vue";
import Assign from "./../icons/Assign.vue";
import Remove from "./../icons/Remove.vue";
import Modal from "./../steps/Modal.vue";
import Multiselect from "vue-multiselect";

export default {
  name: "Header",
  components: {
    Edit,
    EntryAdd,
    Assign,
    Remove,
    Modal,
    Multiselect
  },
  props: {
    entries: Object,
    sheets: Object,
    customPieces: Array,
    customStrategies: Array,
    updated: Boolean
  },
  data: function() {
    return {
      strategyIndex: "",
      pieceIndex: "",
      piece: "",
      strategy: "",
      showModal: false,
      showFolderUpdateModal: false,
      studentSelect: "",
      folderSelect: "",
      sharedEntry: null,
      modalEntry: {},
      myTags: [],
      sheetsActive: "sheets"
    };
  },
  computed: {
    ...mapState({
      userUID: state => state.user.user.uid,
      isInstructor: state => state.instructor.isInstructor,
      myStudents: state => state.instructor.myStudents,
      selection: state => state.container.selection,
      selectionMap: state => state.container.selectionMap,
      selectedFolder: state => state.user.user.selectedFolder
    }),
    ...mapGetters({
      folderCountObj: "user/getFolderCountObj",
      selectedFolderEntries: "user/selectedFolderEntries",
      myFolders: "user/getMyFolders"
    })
  },
  beforeMount: function() {
    let urlRef = window.location.href.substring(
      window.location.href.lastIndexOf("/") + 1
    );
    if (this.selectionMap[urlRef]) {
      this.$store.commit("container/setSelection", urlRef);
    } else {
      this.$store.commit("container/setSelection", "sheets");
    }
  },
  mounted: function() {},
  watch: {},
  methods: {
    filterSheets(sheets,top3){
        let res;
        //selectedFolderEntries
        if(!sheets){
            return {};
        }
        if(this.selectedFolder){
            let entryMap = Object.keys(this.selectedFolderEntries)
                .reduce((entryObj,key)=>{
                    entryObj[this.selectedFolderEntries[key].entryName] = true;
                    return entryObj;
                },{});
                res = Object.keys(sheets).reverse();
                res = res.filter(key=>entryMap[sheets[key].entryName]);
                if(top3){
                    res = res.slice(0,3);
                }
                else {
                    res = res.slice(3);
                }
                return res.reduce((sheetMap,key)=>{
                    sheetMap[key] = sheets[key];
                    return sheetMap;
                },{});
        }
        res = Object.keys(sheets).reverse();
        if(top3){
            res = res.slice(0,3);
        }
        else {
            res = res.slice(3);
        }
        return res.reduce((newSheets,key)=>{
            newSheets[key] = sheets[key];
            return newSheets;
        },{});
    },
    showUpdateFolderModal(entry, i) {
      this.showFolderUpdateModal = true;
      this.modalEntry = { name: entry.entryName, id: i };
      this.folderSelect = entry.folder;
    },
    addTagPiece(tag) {
      if (this.myTags.indexOf(tag) === -1) {
        this.myTags.push(tag);
      }
      this.folderSelect = tag;
    },
    folderUpdateModalClosed() {
      console.log(this.modalEntry.id);
      console.log(this.folderSelect);
      this.$store.dispatch("user/updateEntryFolder", {
        id: this.modalEntry.id,
        folder: this.folderSelect
      });
      this.showFolderUpdateModal = false;
    },
    setSelection(folder) {
      this.$store.commit("user/setSelection", folder);
    },
    goToTemplate() {
      window.history.pushState(null, "", "/");
      this.$store.commit("container/setContainerView", "newEntry");
    },
    getDateOfWeek(w, y) {
      var d = 1 + (w - 1) * 7; // 1st of January + 7 days for each week
      return new Date(y, 0, d);
    },
    formatWeek(week) {
      if (week) {
        let weekParts = week.split("-W");
        let startDate = this.getDateOfWeek(weekParts[1], weekParts[0]),
          startDateString =
            startDate.getMonth() +
            1 +
            "/" +
            startDate.getDate() +
            "/" +
            startDate.getFullYear();
        startDate.setDate(startDate.getDate() + 7);
        let endDateString =
          startDate.getMonth() +
          1 +
          "/" +
          startDate.getDate() +
          "/" +
          startDate.getFullYear();
        return startDateString + " - " + endDateString;
      }
    },
    assignEntry(entry) {
      this.sharedEntry = entry;
      this.showModal = true;
    },
    doAssignEntry() {
      if (this.studentSelect) {
        this.$store
          .dispatch("sheet/addEntrySheet", {
            userUID: this.studentSelect,
            entry: this.sharedEntry
          })
          .then(entryId => {
            alert("entry shared");
            //console.log(entry);
            console.log(entryId);
          });
      }
      this.showModal = false;
    },
    removeEntry(entryId) {
      var remove = confirm("Remove Entry?");
      if (remove) {
        this.$store.dispatch("entry/removeEntry", {
          userUID: this.userUID,
          id: entryId
        });
      }
    },
    removeSheet(sheetId) {
      var remove = confirm("Remove Sheet?");
      if (remove) {
        this.$store.dispatch("user/removeEntrySheet", {
          userUID: this.userUID,
          id: sheetId
        });
      }
    },
    addEntry(entry) {
      let vueThis = this;
      this.$store
        .dispatch("sheet/addEntrySheet", {
          userUID: this.userUID,
          entry: entry
        })
        .then(entryId => {
          //console.log(entry);
          //console.log(entryId);
          //vueThis.$store.commit('container/setHome', false);
          vueThis.$store.commit("container/setHome", false);
          vueThis.$store.commit("container/setContainerView", "sheet");
          vueThis.$store.commit("container/setSheet", true);
          vueThis.$store.commit("sheet/setInstructorOverride", "");
        });
    },
    removeCustomEntry(custom, entryId) {
      this.$store
        .dispatch("entry/removeCustomEntry", {
          userUID: this.userUID,
          custom: custom,
          entryUID: entryId
        })
        .then(() => {
          if (custom === "customPieces") {
            this.piece = "";
            this.pieceIndex = "";
          }
          if (custom === "customStrategies") {
            this.strategy = "";
            this.strategyIndex = "";
          }
        });
    },
    updateStrategy() {
      let label = document.getElementById("stratLabel").value;
      if (label) {
        let id = this.strategyIndex
          ? this.customStrategies[this.strategyIndex].id
          : this.uuidv4();
        let updateStrat = {
          id: id,
          label: label,
          description: document.getElementById("stratDescription").value,
          master: document.getElementById("stratMaster").checked
        };
        let userEntries = {
          strategies: [updateStrat],
          pieces: []
        };
        this.$store
          .dispatch("entry/updateUserEntry", {
            userUID: this.userUID,
            userEntries: userEntries
          })
          .then(() => {
            /*                 setTimeout(()=>{
                    this.strategy = label;
                },1400); */
          });
      }
    },
    updatePiece() {
      let name = document.getElementById("pieceName").value;
      if (name) {
        let id = this.pieceIndex
          ? this.customPieces[this.pieceIndex].id
          : this.uuidv4();
        let updatePiece = {
          id: id,
          name: name,
          source: document.getElementById("pieceSource").value,
          author: document.getElementById("pieceAuthor").value,
          master: document.getElementById("pieceMaster").checked
        };
        let userEntries = {
          strategies: [],
          pieces: [updatePiece]
        };
        this.$store
          .dispatch("entry/updateUserEntry", {
            userUID: this.userUID,
            userEntries: userEntries
          })
          .then(() => {
            /*                 setTimeout(()=>{
                    this.piece = name;
                },1400); */
          });
      }
    },
    strategySelect(ev) {
      let index = ev.currentTarget.options[
        ev.currentTarget.options.selectedIndex
      ].getAttribute("ind");
      if (index) {
        this.strategyIndex = parseInt(index);
      } else {
        this.strategyIndex = "";
      }
    },
    pieceSelect(ev) {
      let index = ev.currentTarget.options[
        ev.currentTarget.options.selectedIndex
      ].getAttribute("ind");
      if (index) {
        this.pieceIndex = parseInt(index);
      } else {
        this.pieceIndex = "";
      }
    },
    goToEntry(entryRef, ev) {
      if (ev) {
        ev.preventDefault();
      }
      this.$store.commit("container/setExistingEntry", this.entries[entryRef]);
      this.$store.commit("container/setEntryUID", entryRef);
      this.$store.commit("container/setHome", false);
      this.$store.commit("container/setContainerView", "newEntry");
      this.$store.commit("container/setNewEntry", true);
    },
    goToSheet(sheetRef, ev) {
      if (ev) {
        ev.preventDefault();
      }
      this.$store.commit("sheet/setActiveSheetId", sheetRef);
      this.$store.commit("sheet/setActiveSheet", this.sheets[sheetRef]);
      this.$store.commit("container/setHome", false);
      this.$store.commit("container/setContainerView", "sheet");
      this.$store.commit("container/setSheet", true);
      this.$store.commit("sheet/setInstructorOverride", "");
    },
    uuidv4() {
      return ([1e7] + -1e3 + -4e3 + -8e3 + -1e11).replace(/[018]/g, c =>
        (
          c ^
          (crypto.getRandomValues(new Uint8Array(1))[0] & (15 >> (c / 4)))
        ).toString(16)
      );
    }
  }
};
</script>

